class InputWSSelect{

    constructor(idSelect){
        this.select = document.getElementById(idSelect);
        this.select.style.display = 'none';
        this.idSelect = idSelect;
        InputWSSelect.createChanger(this.idSelect, this.idSelect);
        //this.IWS = new window.inputWithSearch.getElement()
    }

    static createChanger(id, afterDomElement){
        let fragment = document.createDocumentFragment();
        fragment.innerHTML = this.constructorChanger(id);
        insertAfter(fragment, afterDomElement);
    }

    static constructorChanger(id){
        return `
            <div id="${id}PluginSelectIWS">
                TestData
            </div>
        `;
    }

}

function insertAfter(newNode, referenceNode) {
    referenceNode.parentNode.insertBefore(newNode, referenceNode.nextSibling);
}

window.inputWithSearchSelect = InputWSSelect;